@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

@import './utils/variables.css';

*,
*:before,
*:after {
    box-sizing: border-box;
}

body {
  background: var(--brand-blue);
  font-family: 'Roboto', sans-serif;

  @apply uppercase;
}

h1 {
  text-transform: uppercase;
  font-weight: 700;
  margin: 0;
}

p {
  margin: 5px;
}

a {
  color: #FC5E2F;
  @apply font-bold;
}

.data-viz hgroup {
  color: #fff;
  padding: 2rem;
  display: -ms-grid;
  display: grid;
  grid-template-columns: 55% 40%;
  grid-column-gap: 5vw;
  
}

.data-viz .map {
  display: -ms-grid;
  display: grid;
  grid-template-columns: 15% 85%;
}

.data-viz .map__chart {
  height: 75vh;
  width: 100%;
}

.data-viz .map__legend {
  width: 100%;
  color: #fff;
  padding-left: 1rem;
}
